.contact-us {
  &__info {
    margin: 0 0 24px;
    cursor: pointer;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
  }

  &__info-link {
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    color: inherit;
  }

  &__field {
    margin: 0 0 16px;
  }

  &__field--last {
    margin: 0 0 32px;
  }

}
